import marked      from 'marked';
import "@webcomponents/webcomponentsjs/webcomponents-bundle";
import '@webcomponents/webcomponentsjs/custom-elements-es5-adapter.js';

class BoxE extends HTMLElement {
  
 constructor() {

    super();
   const shadow = this.attachShadow({mode: 'open'});
    let mdStr=this.innerHTML
    let styleBg=this.getAttribute("styleBg")
    styleBg=styleBg==null ? "#aaa2" : styleBg
    const html: string = marked(mdStr)
    shadow.innerHTML=`
    <style>
    .box-e {
      background:${styleBg};
      width:97.5%;
      text-align:left;
    }
    </style>
    <center><div class="box-e" > ${html}</div></center> 
    `
  }
}

customElements.define('box-e', BoxE);


